.WorkspaceTabs {
  height: 45px;
  padding: 0 0 0 0;
  position: absolute;
  top: -45px;
  /* left: 0; */
  overflow:visible;
}
.Tab {
  width: 200px;
  height:45px;
  overflow:hidden;
  float:left;
  margin: 0 -15px 0 0;
  cursor: pointer;
  position: relative;
}

.TabNotification {
  position: absolute;
  right: 13px;
  top: 0px;
  background: red;
  height: 25px;
  width: 25px;
  text-align: center;
  border-radius: 50%;
  color: white;
  -webkit-transition: .2s;
  -o-transition: .2s;
  transition: .2s;
  -webkit-animation: bounce 4s infinite;
          animation: bounce 4s infinite;
  overflow: visible;
  z-index: 2000 !important;
}

@-webkit-keyframes bounce {
  0% {-webkit-transform: rotateY(0deg);transform: rotateY(0deg); }
  20% {-webkit-transform: rotateY(180deg);transform: rotateY(180deg); }
  80% {transoform: rotateY(180deg); }
  100% {-webkit-transform: rotateY(360deg);transform: rotateY(360deg); }
}

@keyframes bounce {
  0% {-webkit-transform: rotateY(0deg);transform: rotateY(0deg); }
  20% {-webkit-transform: rotateY(180deg);transform: rotateY(180deg); }
  80% {transoform: rotateY(180deg); }
  100% {-webkit-transform: rotateY(360deg);transform: rotateY(360deg); }
} 


