:import('../../colors.css'){
  blue: blue;
}
.Edit {
  color: #999;
  cursor: pointer;
  -webkit-transition: .2s;
  -o-transition: .2s;
  transition: .2s;
}

.Edit:hover {
  color: blue;
}

.EditContainer {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: start;
      align-items: flex-start;
}

.EditLine {
  display: -ms-flexbox;
  display: flex;
}

.TextArea {
  width: 100%;
  height: 90%;
  font-size: 19px;
  padding: 5px;
  resize: none;
}

.EditButtons {
  display: -ms-flexbox;
  display: flex;
}

.ToggleEdit {
  color: #999;
  cursor: pointer;
}

.ToggleEdit:hover {
  color: blue;
}


